import { defineComponent, computed, ref } from 'vue';
import { useStore } from '@/store';
import { useRouter, useRoute } from 'vue-router';
import { pathMapToMenu } from '@/utils/map-menus'; // vuex - typescript  => pinia

export default defineComponent({
  props: {
    collapse: {
      type: Boolean,
      default: false
    }
  },

  setup() {
    // 1.图标
    const iconName = ['Platform', 'Setting', 'ShoppingBag', 'ChatLineRound']; // 2.绑定当前激活菜单

    const store = useStore();
    const userMenus = computed(() => store.state.login.userMenus); // router

    const router = useRouter();
    const route = useRoute();
    const currentPath = route.path; // 刷新   菜单定位

    const menu = pathMapToMenu(userMenus.value, currentPath);
    const defaultValue = ref(menu.id + ''); // event handle

    const handleMenuItemClick = item => {
      router.push({
        path: item.url ?? 'not-found'
      });
    };

    return {
      userMenus,
      iconName,
      handleMenuItemClick,
      defaultValue
    };
  }

});